import React, { useState, useEffect } from "react";
import { Button, Container, Row, Table, Col, Form, Modal, DropdownButton, ButtonToolbar, ButtonGroup } from "react-bootstrap";
import "./Reports.scss";
import { API_URL } from "../API_URL";

const Reports = () => {

  interface ReporterType {
    id: number;
    title: string;
    description: string;
    date: string;
    category: string;
    location: string;
    status: string;
    verified_comment: string;
    verified_time: Date;
    media: string;
    reporter_email: string
  }

  const [isLoading, setIsLoading] = useState<Boolean>(false);
  const [data, setData] = useState<Array<ReporterType>>([]);
  const [error, setError] = useState(null);

  // Current category
  const [status, setStatus] = useState("Pending");

  // The checkboxes
  const [ids, setIds] = useState<Array<number>>([]);

  //Add new report
  const [newData, setNewData] = useState<ReporterType>({id:0, title:"", description: "", date: "", category:"", location:"", status:"", verified_comment:"", verified_time: new Date() ,media:"", reporter_email:""});

  //Modal
  const [show, setShow] = useState(false);
  const [modalAction, setModalAction] = useState("");
  const handleClose = () => setShow(false);
  const handleShow = (action:string) => {
    setModalAction(action);
    if (action === "Add") {
      setNewData({id:0, title:"", description: "", date: "", category:"", location:"", status:"", verified_comment:"", verified_time: new Date(), media:"", reporter_email:""});
    }
    setShow(true);
  }

  useEffect(() => {
    setIsLoading(true);

    fetch(API_URL + 'report/status/' + status, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
    })
      .then(response => response.json())
      .then(results => {
        setData(results);
        setIsLoading(false);
      })
      .catch(err => {
        setIsLoading(false);
        setError(err);
      });
  }, [status]);

  //Input inside Modal
  const handleNewDataChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const val = event.target.value;
    const name = event.target.name;

    switch (name) {
      case "title":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { title: val }); 
        });       
        break;
      case "description":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { description: val }); 
        });       
        break;
      case "date":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { date: val }); 
        });       
        break;   
      case "category":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { category: val }); 
        });       
        break;
      case "location":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { location: val }); 
        });       
        break;  
      case "status":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { status: val }); 
        });       
        break;  
      case "verified_comment":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { verified_comment: val }); 
        });       
        break; 
      case "media":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { media: val }); 
        });       
        break;                    
      default:
        break;
    }
  };

    //add report func
    const addreport = (action:string) => {

      var endpoint:string = "";

      if (newData.title !== "" || newData.description !== "" || !newData.date || newData.location !== "" || newData.category !== "" ) {

        switch (action) {
          case "Add":
            endpoint = API_URL + 'report';
            break;
          case "Update":
            endpoint = API_URL + 'report/' + newData.id;
            break;
          default:
            break;
        }

        const payload = newData;

        fetch(endpoint, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(payload),
          })
          .then(async res => { 
            try {
                const jsonRes = await res.json();
                if (res.status !== 200) {
                    console.log(jsonRes.message);
                } else {
                  setNewData({id:0, title:"", description: "", date: "", category:"", location:"", status:"", verified_comment:"", verified_time: new Date(), media:"", reporter_email:""});
                  console.log(jsonRes.message);
                  window.location.reload();
                }
            } catch (err) {
                console.log(err);
            };
        })
        .catch(err => {
            console.log(err);
        });
    
      };        

    }

  const updatereport = (event:React.MouseEvent<HTMLElement>, id:number, title: string, description: string, date: string, category: string, location: string, status: string, verified_comment: string, media: string, reporter_email:string) => {
    event.preventDefault();
    setNewData({ id:id, title:title, description:description, date:date, category:category, location:location, status:status, verified_comment:verified_comment, verified_time: new Date(), media:media, reporter_email:reporter_email});
    handleShow("Update");
  };

  //Checkboxes
    // This function will be triggered when a checkbox changes its state
    const selectItem = (event: React.ChangeEvent<HTMLInputElement>) => {
      const selectedId = parseInt(event.target.value);
  
      // Check if "ids" contains "selectedIds"
      // If true, this checkbox is already checked
      // Otherwise, it is not selected yet
      if (ids.includes(selectedId)) {
        const newIds = ids.filter((id) => id !== selectedId);
        setIds(newIds);
      } else {
        const newIds = [...ids];
        newIds.push(selectedId);
        setIds(newIds);
      }
    };
  
    // This function will be called when the "REMOVE SELECTED USERS" is clicked
    const removeItems = () => {

      const payload = ids;
      fetch(API_URL + 'report/' + payload, {
          method: 'DELETE',
          headers: {
              'Content-Type': 'application/json',
          },
          body: JSON.stringify(payload),
        })
        .then(async res => { 
          try {
              const jsonRes = await res.json();
              if (res.status !== 200) {
                  console.log(jsonRes.message);
              } else {
                console.log(jsonRes.message);
                window.location.reload();
              }
          } catch (err) {
              console.log(err);
          };
      })
      .catch(err => {
          console.log(err);
      });
    };

        const verify = () => {

          //make if else case to check user
          const payload = ids;
          // const payload = {id: ids, verified_time: new Date(), verified_comment: comment, status: "Verified"};
          // console.log(payload);

          fetch(API_URL + 'report/verify/' + payload, {
              method: 'POST',
              headers: {
                  'Content-Type': 'application/json',
              },
              body: JSON.stringify(payload),
            })
            .then(async res => { 
              try {
                  const jsonRes = await res.json();
                  if (res.status !== 200) {
                      console.log(jsonRes.message);
                  } else {
                    console.log(jsonRes.message);
                    window.location.reload();
                  }
              } catch (err) {
                  console.log(err);
              };
          })
          .catch(err => {
              console.log(err);
          });
          
        };

  return (
    <>
    <Container>
        <h3>Reports</h3>
        <Row className="py-2">
        <Col className="status-button">
            <Button className={status == "Pending" ? "button pending active" : "button"} onClick={()=>setStatus("Pending")} >Pending</Button>
            <Button className={status == "Verified" ? "button waiting active" : "button"} onClick={()=>setStatus("Verified")}>Verified</Button>
            <Button className={status == "In Progress" ? "button inprogress active" : "button"} onClick={()=>setStatus("In Progress")}>In Progress</Button>
            <Button className={status == "Solved" ? "button solved active" : "button"} onClick={()=>setStatus("Solved")}>Solved</Button>
            <Button className={status == "Rejected" ? "button rejected active" : "button"} onClick={()=>setStatus("Rejected")}>Rejected</Button>
        </Col>
        </Row>
        <Row className="p-2">
          <Table striped bordered hover responsive>
            <thead>
              <tr>
                <th>Title</th>
                <th>Reporter</th>
                <th>Date</th>
                <th>Category</th>
                <th>Location</th>
                <th>Status</th>
                {/* <th>Media</th>         */}
                <th>Action</th>          
              </tr>
            </thead>
            <tbody>
                {
                  data.map((item:ReporterType, index:number) => (
                    <tr key={`report-${index}`} className={`report-${index}`}>
                      <td className="title">
                      <a href="#" onClick={(e) => {
                          updatereport(e, item.id, item.title, item.description, item.date, item.category, item.location, item.status, item.verified_comment, item.media, item.reporter_email)
                        }}>{item.title}</a> 
                      </td>
                      <td className="reporter_email">
                        {item.reporter_email}
                      </td>
                      <td className="date">
                        {item.date.split('T')[0]}
                      </td>
                      <td className="category">
                        {item.category}
                      </td>
                      <td className="location">
                        {item.location}
                      </td>
                      <td className="status">
                        {item.status}
                      </td>
                      {/* <td className="media">
                        {item.media}
                      </td> */}
                      <td className="action">
                        
                      <Form.Check
                        type="checkbox"
                        value={item.id}
                        onChange={selectItem}
                        isValid={ids.includes(item.id) ? true : false}
                      />
                      </td>
                    </tr>
                  ))
                }
            </tbody>
          </Table>
        </Row>
        <Row>
        <Col md={{ span: 8, offset: 4 }} style={{ textAlign:"right"}}>
          {ids.length !== 0 ? 
          <>
            <Button style={{ backgroundColor:"red"}} onClick={removeItems}>Delete</Button>
            {status === "Pending" ? 
            <Button style={{ backgroundColor:"green", marginLeft:"0.6rem"}} onClick={verify}>Verify and Forward</Button>
            : <></>}
          </> : <></>
          }
          </Col>
        </Row>
    </Container>

    <Modal show={show} aria-labelledby="contained-modal-title-vcenter" onHide={handleClose} centered >
        <Modal.Header closeButton>
          <Modal.Title>{modalAction} report</Modal.Title>
        </Modal.Header>
        <Modal.Body>
        <Form >
          <Form.Group className="mb-3" controlId="title">
            <Form.Label>Report Title</Form.Label>
            {modalAction === "Update" ? (
            <Form.Control type="text" placeholder="Enter Report Title" size="sm" name="title" value={newData.title} onChange={handleNewDataChange} />
            ) : 
            <Form.Control type="text" placeholder="Enter Report Title" size="sm" name="title" value={newData.title} onChange={handleNewDataChange} />
            }
          </Form.Group>
          <Form.Group className="mb-3" controlId="description">
            <Form.Label>Description</Form.Label>
            <Form.Control type="text" as="textarea" rows={3} placeholder="Enter State" size="sm" name="description" value={newData.description} onChange={handleNewDataChange}/>
          </Form.Group>

          <Form.Group className="mb-3" controlId="date">
            <Form.Label>Date</Form.Label>
            <Form.Control type="date" placeholder="Enter Date" size="sm" name="date" value={newData.date} onChange={handleNewDataChange}/>
          </Form.Group>
          
          <Form.Group className="mb-3" controlId="category">
            <Form.Label>Category</Form.Label>
            <Form.Control as="select" placeholder="Enter Category" size="sm" name="category" value={newData.category} onChange={handleNewDataChange}>
            <option>Environment</option>
            <option>Facilities</option>
            <option>Others</option>
            </Form.Control>
          </Form.Group>

          <Form.Group className="mb-3" controlId="location">
            <Form.Label>Location</Form.Label>
            <Form.Control type="text" placeholder="Enter Location" size="sm" name="location" value={newData.location} onChange={handleNewDataChange}/>
          </Form.Group>

          {status === "Pending" || "Verified" ? 
          <Form.Group className="mb-3" controlId="status">
            <Form.Label>Status</Form.Label>
            <Form.Control as="select" placeholder="Enter Status" size="sm" name="status" value={newData.status} onChange={handleNewDataChange}>
            <option>Pending</option>
            <option>Verified</option>
            </Form.Control>
          </Form.Group>
          : 
          <Form.Group className="mb-3" controlId="status">
            <Form.Label>Status</Form.Label>
            <Form.Control as="select" placeholder="Enter Status" size="sm" name="status" value={newData.status} readOnly>
            </Form.Control>
          </Form.Group>
        }


          {newData.status === "Verified" ?           
          <Form.Group className="mb-3" controlId="verified_comment">
            <Form.Label>Comment</Form.Label>
            <Form.Control type="text" as="textarea" rows={3} placeholder="Enter Comment" size="sm" name="verified_comment" value={newData.verified_comment} onChange={handleNewDataChange}/>
          </Form.Group>
          : <></>}


          <Form.Group className="mb-3" controlId="media">
            <Form.Label>Media</Form.Label>
            <Form.Control type="text" placeholder="Enter Media" size="sm" name="media" value={newData.media} onChange={handleNewDataChange}/>
          </Form.Group>

        </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={(e) => {addreport(modalAction)}}>
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>

      
    </>
  );

};
export default Reports;