import React, { useState, useEffect } from "react";
import { Button, Container, Row, Table, Col, Form, Modal } from "react-bootstrap";
import { API_URL } from "../API_URL";

const Products = () => {

  interface ProductType {
    gtin : number;
    productName: string;
    material: string;
    recyclable: string;
    status: string;
    submit_email: string;
  }

  const [isLoading, setIsLoading] = useState<Boolean>(false);
  const [data, setData] = useState<Array<ProductType>>([]);
  const [error, setError] = useState(null);

  //status
  const [status, setStatus] = useState("");

  // The checkboxes
  const [ids, setIds] = useState<Array<number>>([]);

  //Add new product
  const [newData, setNewData] = useState<ProductType>({gtin:0, productName:"", material:"", recyclable:"", status:"Pending", submit_email:"" });

  //Modal
  const [show, setShow] = useState(false);
  const [modalAction, setModalAction] = useState("");
  const handleClose = () => setShow(false);
  const handleShow = (action:string) => {
    setModalAction(action);
    if (action === "Add") {
      setNewData({gtin:0, productName:"", material:"", recyclable:"", status:"Pending", submit_email:"" });
    }
    setShow(true);
  }

  useEffect(() => {
    setIsLoading(true);

    var endpoint:string = "";
    if (status === "") {
      endpoint = API_URL + "product/";      
    }else{
      endpoint = API_URL + "product/status/" + status;
      console.log(endpoint);
    }

    fetch(endpoint, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
    })
      .then(response => response.json())
      .then(results => {
        setData(results);
        setIsLoading(false);
      })
      .catch(err => {
        setIsLoading(false);
        setError(err);
      });
  }, [status]);

  //Input inside Modal
  const handleNewDataChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const val = event.target.value;
    const name = event.target.name;

    switch (name) {
      case "gtin":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { gtin: val }); 
        });       
        break;
      case "name":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { productName: val }); 
        });       
        break;
      case "material":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { material: val }); 
        });       
        break;   
      case "recyclable":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { recyclable: val }); 
        });       
        break;                 
      default:
        break;
    }
  };

    //add product func
    const addProduct = (action:string) => {

      var endpoint:string = "";

      if (newData.gtin !== 0 || newData.productName !== "" || newData.recyclable !== "" || newData.material !== "") {

        switch (action) {
          case "Add":
            endpoint = API_URL + 'product';
            break;
          case "Update":
            endpoint = API_URL + 'product/' + newData.gtin;
            break;
          default:
            break;
        }

        const payload = newData;

        fetch(endpoint, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(payload),
          })
          .then(async res => { 
            try {
                const jsonRes = await res.json();
                if (res.status !== 200) {
                    console.log(jsonRes.message);
                } else {
                  setNewData({gtin:0, productName:"", material:"", recyclable:"", status:"Pending", submit_email:"" });
                  console.log(jsonRes.message);
                  window.location.reload();
                }
            } catch (err) {
                console.log(err);
            };
        })
        .catch(err => {
            console.log(err);
        });
    
      };        

    }

  const updateProduct = (event:React.MouseEvent<HTMLElement>, gtin:number, productName:string, material:string, recyclable:string, status:string, submit_email:string) => {
    event.preventDefault();
    setNewData({gtin:gtin, productName:productName, material:material, recyclable:recyclable, status:status, submit_email:submit_email});
    handleShow("Update");
  };

  //Checkboxes
    // This function will be triggered when a checkbox changes its state
    const selectItem = (event: React.ChangeEvent<HTMLInputElement>) => {
      const selectedId = parseInt(event.target.value);
  
      // Check if "ids" contains "selectedIds"
      // If true, this checkbox is already checked
      // Otherwise, it is not selected yet
      if (ids.includes(selectedId)) {
        const newIds = ids.filter((id) => id !== selectedId);
        setIds(newIds);
      } else {
        const newIds = [...ids];
        newIds.push(selectedId);
        setIds(newIds);
      }
    };
  
    // This function will be called when the "REMOVE SELECTED USERS" is clicked
    const removeItems = () => {

      const payload = ids;
      fetch(API_URL + 'product/' + payload, {
          method: 'DELETE',
          headers: {
              'Content-Type': 'application/json',
          },
          body: JSON.stringify(payload),
        })
        .then(async res => { 
          try {
              const jsonRes = await res.json();
              if (res.status !== 200) {
                  console.log(jsonRes.message);
              } else {
                console.log(jsonRes.message);
                window.location.reload();
              }
          } catch (err) {
              console.log(err);
          };
      })
      .catch(err => {
          console.log(err);
      });
    };

            // This function will be called when the "SUBMIT" is clicked
            const submitItems = () => {

              const payload = {
                status: "Waiting"
              };
              fetch(API_URL + 'product/status/' + ids, {
                  method: 'POST',
                  headers: {
                      'Content-Type': 'application/json',
                  },
                  body: JSON.stringify(payload),
                })
                .then(async res => { 
                  try {
                      const jsonRes = await res.json();
                      if (res.status !== 200) {
                          console.log(jsonRes.message);
                      } else {
                        console.log(jsonRes.message);
                        window.location.reload();
                      }
                  } catch (err) {
                      console.log(err);
                  };
              })
              .catch(err => {
                  console.log(err);
              });
            };


  return (
    <>
    <Container>
        <h3>Products</h3>
        <Row className="py-2">
        <Col className="status-button">
            <Button className={status == "" ? "button all active" : "button"} onClick={()=>setStatus("")} >All</Button>
            <Button className={status == "Pending" ? "button pending active" : "button"} onClick={()=>setStatus("Pending")} >Pending</Button>
            <Button className={status == "Waiting" ? "button waiting active" : "button"} onClick={()=>setStatus("Waiting")} >Waiting to be approved</Button>
            <Button className={status == "Approved" ? "button solved active" : "button"} onClick={()=>setStatus("Approved")}>Approved</Button>
            <Button className={status == "Rejected" ? "button rejected active" : "button"} onClick={()=>setStatus("Rejected")}>Rejected</Button>
        </Col>
        </Row>
        <Row className="p-2">
          <Table striped bordered hover responsive>
            <thead>
              <tr>
                <th>GTIN</th>
                <th>Product Name</th>
                <th>Recyclable</th>
                <th>Material</th>
                <th>Status</th>
                <th>Action</th>
              </tr>
            </thead>
            <tbody>
                {
                  data.map((item:ProductType, index:number) => (
                    <tr key={`product-${index}`} className={`product-${index}`}>
                      <td className="gtin">
                        {item.gtin}
                      </td>
                      <td className="productname">
                        <a href="#" onClick={(e) => {
                          updateProduct(e, item.gtin, item.productName, item.material, item.recyclable, item.status, item.submit_email)
                        }}>{item.productName}</a>
                      </td>
                      <td className="recyclable">
                        {item.recyclable}
                      </td>
                      <td className="material">
                        {item.material}
                      </td>
                      <td className="status">
                        {item.status}
                      </td>
                      <td className="action">
                        
                      <Form.Check
                        type="checkbox"
                        value={item.gtin}
                        onChange={selectItem}
                        isValid={ids.includes(item.gtin) ? true : false}
                      />
                      </td>
                    </tr>
                  ))
                }
            </tbody>
          </Table>
        </Row>
        <Row>
        <Col md={{ span: 4, offset: 8 }} style={{ textAlign:"right"}}>
          {ids.length === 0 ? 
          <>
            <Button style={{ backgroundColor:"purple", marginRight:"10px"}}>Load From Excel File</Button>
            <Button style={{ backgroundColor:"green"}} onClick={(e) => {handleShow("Add")}}>Add New</Button>
          </>
          : 
          <>
            <Button style={{ backgroundColor:"red"}} onClick={removeItems}>Delete</Button>
            <Button style={{ backgroundColor:"green", marginLeft:"0.6rem"}} onClick={submitItems}>Submit</Button>
          </>
          }
          </Col>
        </Row>
    </Container>

    <Modal show={show} aria-labelledby="contained-modal-title-vcenter" onHide={handleClose} centered >
        <Modal.Header closeButton>
          <Modal.Title>{modalAction} Product</Modal.Title>
        </Modal.Header>
        <Modal.Body>
        <Form >
          <Form.Group className="mb-3" controlId="gtin">
            <Form.Label>Product GTIN</Form.Label>
            {modalAction === "Update" ? (
            <Form.Control type="text" placeholder="Enter Product GTIN" size="sm" name="gtin" value={newData.gtin} onChange={handleNewDataChange} readOnly/>
            ) : 
            <Form.Control type="text" placeholder="Enter Product GTIN" size="sm" name="gtin" value={newData.gtin} onChange={handleNewDataChange} />
            }
          </Form.Group>
          <Form.Group className="mb-3" controlId="productName">
            <Form.Label>Product Name</Form.Label>
            <Form.Control type="text" placeholder="Enter Product Name" size="sm" name="name" value={newData.productName} onChange={handleNewDataChange}/>
          </Form.Group>
          
          <Form.Group className="mb-3" controlId="recyclable">
            <Form.Label>Recyclable?</Form.Label>
            <Form.Control as="select" placeholder="Choose Recyclable" size="sm" name="recyclable" value={newData.recyclable} onChange={handleNewDataChange}>
              <option value="yes">Yes</option>
              <option value="no">No</option>
              <option value="partly">Partly</option>
            </Form.Control>
          </Form.Group>

        {newData.recyclable == "no" ? <></>
        : 
          <Form.Group className="mb-3" controlId="material">
            <Form.Label>Material</Form.Label>
            <Form.Control as="select" placeholder="Choose Material" size="sm" name="material" value={newData.material} onChange={handleNewDataChange}>
              <option value="Yes">Yes</option>
              <option value="No">No</option>
              <option value="Partly">Partly</option>
            </Form.Control>
          </Form.Group>
        }

          
          <Form.Group className="mb-3" controlId="submit_email">
            <Form.Label>Submit by</Form.Label>
            <Form.Control type="text" size="sm" name="submit_email" value={newData.submit_email} readOnly/>
          </Form.Group>

        </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={(e) => {addProduct(modalAction)}}>
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
};


export default Products;