import React, { useState, useEffect } from "react";
import { Button, Container, Row, Table, Col, Form, Modal } from "react-bootstrap";
import { API_URL } from "../API_URL";

const Materials = () => {

  interface MaterialType {
    id: number;
    material: string;
    time: string;
    guide: string;
    category: string,
    recyclable: string,
    non_recyclable: string
  }

  const [isLoading, setIsLoading] = useState<Boolean>(false);
  const [data, setData] = useState<Array<MaterialType>>([]);
  const [error, setError] = useState(null);

  // The checkboxes
  const [ids, setIds] = useState<Array<number>>([]);

  //Add new user
  const [newData, setNewData] = useState<MaterialType>({id:0, material:"", time:"", guide:"", category:"", recyclable:"", non_recyclable:""});

  //Modal
  const [show, setShow] = useState(false);
  const [modalAction, setModalAction] = useState("");
  const handleClose = () => setShow(false);
  const handleShow = (action:string) => {
    setModalAction(action);
    if (action === "Add") {
      setNewData({id:0, material:"", time:"", guide:"", category:"", recyclable:"", non_recyclable:""});
    }
    setShow(true);
  }

  useEffect(() => {
    setIsLoading(true);

    fetch(API_URL + 'material', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
    })
      .then(response => response.json())
      .then(results => {
        setData(results);
        setIsLoading(false);
      })
      .catch(err => {
        setIsLoading(false);
        setError(err);
      });
  }, []);

  //Input inside Modal
  const handleNewDataChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const val = event.target.value;
    const name = event.target.name;

    switch (name) {
      case "material":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { material: val }); 
        });       
        break;
      case "time":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { time: val }); 
        });       
        break;
      case "guide":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { guide: val }); 
        });       
        break;      
      case "category":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { category: val }); 
        });       
        break;
      case "recyclable":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { recyclable: val }); 
        });       
        break;
      case "non_recyclable":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { non_recyclable: val }); 
        });       
        break;               
      default:
        break;
    }
  };

    //add user func
    const addMaterial = (action:string) => {

      var endpoint:string = "";

      if (newData.material !== "" || newData.time !== "" || newData.guide !== "" || newData.category !== "" ) {

        switch (action) {
          case "Add":
            endpoint = API_URL + 'material';
            break;
          case "Update":
            endpoint = API_URL + 'material/' + newData.id;
            break;
          default:
            break;
        }

        const payload = newData;

        fetch(endpoint, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(payload),
          })
          .then(async res => { 
            try {
                const jsonRes = await res.json();
                if (res.status !== 200) {
                    console.log(jsonRes.message);
                } else {
                  setNewData({ id:0, material:"", time: "", guide:"", category:"", recyclable:"", non_recyclable:"" });
                  console.log(jsonRes.message);
                  window.location.reload();
                }
            } catch (err) {
                console.log(err);
            };
        })
        .catch(err => {
            console.log(err);
        });
    
      };        

    }

  const updateuser = (event:React.MouseEvent<HTMLElement>, id:number, material: string, time: string, guide: string, category:string, recyclable:string, non_recyclable:string) => {
    event.preventDefault();
    setNewData({ id:id, material:material, time:time, guide:guide, category:category, recyclable:recyclable, non_recyclable:non_recyclable});
    handleShow("Update");
  };

  //Checkboxes
    // This function will be triggered when a checkbox changes its state
    const selectItem = (event: React.ChangeEvent<HTMLInputElement>) => {
      const selectedId = parseInt(event.target.value);
  
      // Check if "ids" contains "selectedIds"
      // If true, this checkbox is already checked
      // Otherwise, it is not selected yet
      if (ids.includes(selectedId)) {
        const newIds = ids.filter((id) => id !== selectedId);
        setIds(newIds);
      } else {
        const newIds = [...ids];
        newIds.push(selectedId);
        setIds(newIds);
      }
    };
  
    // This function will be called when the "REMOVE SELECTED USERS" is clicked
    const removeItems = () => {

      const payload = ids;
      console.log(payload);
      fetch(API_URL + 'material/' + payload, {
          method: 'DELETE',
          headers: {
              'Content-Type': 'application/json',
          },
          body: JSON.stringify(payload),
        })
        .then(async res => { 
          try {
              const jsonRes = await res.json();
              if (res.status !== 200) {
                  console.log(jsonRes.message);
              } else {
                console.log(jsonRes.message);
                window.location.reload();
              }
          } catch (err) {
              console.log(err);
          };
      })
      .catch(err => {
          console.log(err);
      });
    };

  return (
    <>
    <Container>
        <h3>Materials</h3>
        <Row className="p-2">
          <Table striped bordered hover responsive>
            <thead>
              <tr>
                <th>ID</th>
                <th>Category</th>
                <th>Material</th>
                <th>Biodegradable Time</th>
                <th>Recycle Guide</th>
                <th>Recyclable</th>
                <th>Non-recyclable</th>
                <th>Action</th>
              </tr>
            </thead>
            <tbody>
                {
                  data.map((item:MaterialType, index:number) => (
                    <tr key={`user-${index}`} className={`user-${index}`}>
                      <td className="id">
                        {item.id}
                      </td>
                      <td className="category">
                        {item.category}
                      </td>
                      <td className="material">
                        <a href="#" onClick={(e) => {
                          updateuser(e, item.id, item.material, item.time, item.guide, item.category, item.recyclable, item.non_recyclable)
                        }}>{item.material}</a>
                      </td>
                      <td className="time">
                        {item.time}
                      </td>
                      <td className="guide">
                        {item.guide}
                      </td>
                      <td className="recyclable">
                        {item.recyclable}
                      </td>
                      <td className="non_recyclable">
                        {item.non_recyclable}
                      </td>
                      <td className="action">  
                      <Form.Check
                        type="checkbox"
                        value={item.id}
                        onChange={selectItem}
                        isValid={ids.includes(item.id) ? true : false}
                      />
                      </td>
                    </tr>
                  ))
                }
            </tbody>
          </Table>
        </Row>
        <Row>
        <Col md={{ span: 8, offset: 4 }} style={{ textAlign:"right"}}>
          {ids.length === 0 ? 
          <>
            <Button style={{ backgroundColor:"green"}} onClick={(e) => {handleShow("Add")}}>Add New</Button>
          </>
          : 
            <Button style={{ backgroundColor:"red"}} onClick={removeItems}>Delete</Button>
          }
          </Col>
        </Row>
    </Container>

    <Modal show={show} aria-labelledby="contained-modal-title-vcenter" onHide={handleClose} centered >
        <Modal.Header closeButton>
          <Modal.Title>{modalAction} material</Modal.Title>
        </Modal.Header>
        <Modal.Body>
        <Form >
          <Form.Group className="mb-3" controlId="category">
            <Form.Label>Category</Form.Label>
            <Form.Control type="text" placeholder="Enter Category" size="sm" name="category" value={newData.category} onChange={handleNewDataChange} />
          </Form.Group>
          <Form.Group className="mb-3" controlId="material">
            <Form.Label>Material</Form.Label>
            <Form.Control type="text" placeholder="Enter Material" size="sm" name="material" value={newData.material} onChange={handleNewDataChange} />
          </Form.Group>
          <Form.Group className="mb-3" controlId="time">
            <Form.Label>Biodegradable Time</Form.Label>
            <Form.Control type="text" placeholder="Enter Time" size="sm" name="time" value={newData.time} onChange={handleNewDataChange}/>
          </Form.Group>
          <Form.Group className="mb-3" controlId="guide">
            <Form.Label>Recycle Guide</Form.Label>
            <Form.Control type="text" placeholder="Enter Guide" size="sm" name="guide" value={newData.guide} onChange={handleNewDataChange}/>
          </Form.Group>
          <Form.Group className="mb-3" controlId="recyclable">
            <Form.Label>Recyclable Items</Form.Label>
            <Form.Control type="text" placeholder="Enter Recyclable Items" size="sm" name="recyclable" value={newData.recyclable} onChange={handleNewDataChange}/>
          </Form.Group>
          <Form.Group className="mb-3" controlId="non_recyclable">
            <Form.Label>Non-Recyclable Items</Form.Label>
            <Form.Control type="text" placeholder="Enter Non-Recyclable Items" size="sm" name="non_recyclable" value={newData.non_recyclable} onChange={handleNewDataChange}/>
          </Form.Group>
        </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={(e) => {addMaterial(modalAction)}}>
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );

};
export default Materials;