import React, { useState, useEffect } from "react";
import { Button, Container, Row, Table, Col, Form, Modal } from "react-bootstrap";
import { API_URL } from "../API_URL";

const Collectors = () => {

  interface CollectorType {
    id: number;
    daerah: string;
    name: string;
    alamat: string;
    telefon: string;
    faks: string;
    pic: string;
    type: string;
    lat: number;
    long: number;
    status: string;
    category: string;
  }

  const [isLoading, setIsLoading] = useState<Boolean>(false);
  const [data, setData] = useState<Array<CollectorType>>([]);
  const [error, setError] = useState(null);

  // The checkboxes
  const [ids, setIds] = useState<Array<number>>([]);

  //Add new collector
  const [newData, setNewData] = useState<CollectorType>({id:0, daerah:"", name: "", alamat:"", telefon:"", faks:"", pic:"", type:"", lat:0, long:0, status:"pending", category:""});

  //Modal
  const [show, setShow] = useState(false);
  const [modalAction, setModalAction] = useState("");
  const handleClose = () => setShow(false);
  const handleShow = (action:string) => {
    setModalAction(action);
    if (action === "Add") {
      setNewData({id:0, daerah:"", name: "", alamat:"", telefon:"", faks:"", pic:"", type:"", lat:0, long:0, status:"pending", category:""});
    }
    setShow(true);
  }

  useEffect(() => {
    setIsLoading(true);

    fetch(API_URL + 'collector', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
    })
      .then(response => response.json())
      .then(results => {
        setData(results);
        setIsLoading(false);
      })
      .catch(err => {
        setIsLoading(false);
        setError(err);
      });
  }, []);

  //Input inside Modal
  const handleNewDataChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const val = event.target.value;
    const name = event.target.name;

    switch (name) {
      case "daerah":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { daerah: val }); 
        });       
        break;
      case "name":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { name: val }); 
        });       
        break;
      case "alamat":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { alamat: val }); 
        });       
        break;   
      case "telefon":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { telefon: val }); 
        });       
        break;
      case "faks":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { faks: val }); 
        });       
        break;  
      case "pic":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { pic: val }); 
        });       
        break;  
      case "type":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { type: val }); 
        });       
        break;  
      case "lat":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { lat: val }); 
        });       
        break;       
      case "long":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { long: val }); 
        });       
        break; 
      case "category":
        setNewData((prevVal) => {
          return Object.assign({}, prevVal, { category: val }); 
        });       
        break;                    
      default:
        break;
    }
  };

    //add collector func
    const addcollector = (action:string) => {

      var endpoint:string = "";

      if (newData.daerah !== "" || newData.name !== "" || newData.alamat !== "" || newData.telefon !== "" || newData.faks !== "" || newData.pic !== "" || newData.type !== "" || newData.lat !== 0 || newData.long !== 0 || newData.category !== "" ) {

        switch (action) {
          case "Add":
            endpoint = API_URL + 'collector';
            break;
          case "Update":
            endpoint = API_URL + 'collector/' + newData.id;
            break;
          default:
            break;
        }

        const payload = newData;

        fetch(endpoint, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(payload),
          })
          .then(async res => { 
            try {
                const jsonRes = await res.json();
                if (res.status !== 200) {
                    console.log(jsonRes.message);
                } else {
                  setNewData({ id:0, daerah:"", name: "", alamat:"", telefon:"", faks:"", pic:"", type:"", lat:0, long:0, status:"pending", category:"" });
                  console.log(jsonRes.message);
                  window.location.reload();
                }
            } catch (err) {
                console.log(err);
            };
        })
        .catch(err => {
            console.log(err);
        });
    
      };        

    }

  const updatecollector = (event:React.MouseEvent<HTMLElement>, id:number, daerah: string, name: string, alamat: string, telefon: string, faks: string, pic: string, type: string, lat: number, long: number, status: string, category: string) => {
    event.preventDefault();
    setNewData({ id:id, daerah:daerah, name:name, alamat:alamat, telefon:telefon, faks:faks, pic:pic, type:type, lat:lat, long:long, status:status, category:category});
    handleShow("Update");
  };

  //Checkboxes
    // This function will be triggered when a checkbox changes its state
    const selectItem = (event: React.ChangeEvent<HTMLInputElement>) => {
      const selectedId = parseInt(event.target.value);
  
      // Check if "ids" contains "selectedIds"
      // If true, this checkbox is already checked
      // Otherwise, it is not selected yet
      if (ids.includes(selectedId)) {
        const newIds = ids.filter((id) => id !== selectedId);
        setIds(newIds);
      } else {
        const newIds = [...ids];
        newIds.push(selectedId);
        setIds(newIds);
      }
    };
  
    // This function will be called when the "REMOVE SELECTED USERS" is clicked
    const removeItems = () => {

      const payload = ids;
      fetch(API_URL + 'collector/' + payload, {
          method: 'DELETE',
          headers: {
              'Content-Type': 'application/json',
          },
          body: JSON.stringify(payload),
        })
        .then(async res => { 
          try {
              const jsonRes = await res.json();
              if (res.status !== 200) {
                  console.log(jsonRes.message);
              } else {
                console.log(jsonRes.message);
                window.location.reload();
              }
          } catch (err) {
              console.log(err);
          };
      })
      .catch(err => {
          console.log(err);
      });
    };

    //scrap data
    const scrap = () => {
      fetch(API_URL + 'collectorscrap', {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        },
      })
      .then(response => response.json())
      .then( results => {

        var temp:CollectorType = {
          id: results[5][0],
          daerah: results[5][1],
          name: results[5][2],
          alamat: results[5][3],
          telefon: results[5][4],
          faks: results[5][5],
          pic: results[5][6],
          type: results[5][7],
          lat: results[5][8],
          long: results[5][9],
          status: "pending",
          category: "General Waste"
        }

        setNewData(temp);
        addcollector("Add");

        // handleShow("Add");
        // addcollector("Add");
      })
      .catch(err => {
        console.warn(err);
        // setIsLoading(false);
        // setError(err);
      });
    }


  return (
    <>
    <Container>
        <h3>Collectors</h3>
        <Row className="p-2">
          <Table striped bordered hover responsive>
            <thead>
              <tr>
                <th>State</th>
                <th>Name</th>
                <th>Address</th>
                <th>Phone No</th>
                <th>Fax No</th>
                <th>Person-in-charge</th>
                <th>Type</th>
                <th>Latitude</th>
                <th>Longitude</th>
                <th>Category</th>
                <th>Status</th>             
              </tr>
            </thead>
            <tbody>
                {
                  data.map((item:CollectorType, index:number) => (
                    <tr key={`collector-${index}`} className={`collector-${index}`}>
                      <td className="daerah">
                        {item.daerah}
                      </td>
                      <td className="name">
                        <a href="#" onClick={(e) => {
                          updatecollector(e, item.id, item.daerah, item.name, item.alamat, item.telefon, item.faks, item.pic, item.type, item.lat, item.long, item.status, item.category)
                        }}>{item.name}</a>
                      </td>
                      <td className="alamat">
                        {item.alamat}
                      </td>
                      <td className="telefon">
                        {item.telefon}
                      </td>
                      <td className="faks">
                        {item.faks}
                      </td>
                      <td className="pic">
                        {item.pic}
                      </td>
                      <td className="type">
                        {item.type}
                      </td>
                      <td className="lat">
                        {item.lat}
                      </td>
                      <td className="long">
                        {item.long}
                      </td>
                      <td className="long">
                        {item.category}
                      </td>
                      <td className="status">
                        {item.status}
                      </td>
                      <td className="action">
                        
                      <Form.Check
                        type="checkbox"
                        value={item.id}
                        onChange={selectItem}
                        isValid={ids.includes(item.id) ? true : false}
                      />
                      </td>
                    </tr>
                  ))
                }
            </tbody>
          </Table>
        </Row>
        <Row>
        <Col md={{ span: 8, offset: 4 }} style={{ textAlign:"right"}}>
          {ids.length === 0 ? 
          <>
            <Button style={{ backgroundColor:"brown", marginRight:"10px"}} onClick={scrap}>Load from SWCorp website</Button>
            <Button style={{ backgroundColor:"purple", marginRight:"10px"}}>Load From Excel File</Button>
            <Button style={{ backgroundColor:"green"}} onClick={(e) => {handleShow("Add")}}>Add New</Button>
          </>
          : 
            <Button style={{ backgroundColor:"red"}} onClick={removeItems}>Delete</Button>
          }
          </Col>
        </Row>
    </Container>

    <Modal show={show} aria-labelledby="contained-modal-title-vcenter" onHide={handleClose} centered >
        <Modal.Header closeButton>
          <Modal.Title>{modalAction} collector</Modal.Title>
        </Modal.Header>
        <Modal.Body>
        <Form >
          <Form.Group className="mb-3" controlId="name">
            <Form.Label>Collector Name</Form.Label>
            {modalAction === "Update" ? (
            <Form.Control type="text" placeholder="Enter Collector Name" size="sm" name="name" value={newData.name} onChange={handleNewDataChange} />
            ) : 
            <Form.Control type="text" placeholder="Enter Collector Name" size="sm" name="name" value={newData.name} onChange={handleNewDataChange} />
            }
          </Form.Group>
          <Form.Group className="mb-3" controlId="daerah">
            <Form.Label>State</Form.Label>
            <Form.Control type="text" placeholder="Enter State" size="sm" name="daerah" value={newData.daerah} onChange={handleNewDataChange}/>
          </Form.Group>

          <Form.Group className="mb-3" controlId="alamat">
            <Form.Label>Address</Form.Label>
            <Form.Control type="text" placeholder="Enter Address" size="sm" name="alamat" value={newData.alamat} onChange={handleNewDataChange}/>
          </Form.Group>
          
          <Form.Group className="mb-3" controlId="telefon">
            <Form.Label>Phone No</Form.Label>
            <Form.Control type="text" placeholder="Enter Phone No" size="sm" name="telefon" value={newData.telefon} onChange={handleNewDataChange}/>
          </Form.Group>

          <Form.Group className="mb-3" controlId="faks">
            <Form.Label>Fax No</Form.Label>
            <Form.Control type="text" placeholder="Enter Fax No" size="sm" name="faks" value={newData.faks} onChange={handleNewDataChange}/>
          </Form.Group>

          <Form.Group className="mb-3" controlId="pic">
            <Form.Label>Person-in-charge</Form.Label>
            <Form.Control type="text" placeholder="Enter Person-in-charge" size="sm" name="pic" value={newData.pic} onChange={handleNewDataChange}/>
          </Form.Group>

          <Form.Group className="mb-3" controlId="type">
            <Form.Label>Type</Form.Label>
            <Form.Control type="text" placeholder="Enter Type" size="sm" name="type" value={newData.type} onChange={handleNewDataChange}/>
          </Form.Group>

          <Form.Group className="mb-3" controlId="category">
            <Form.Label>Category</Form.Label>
            <Form.Control type="text" placeholder="Enter Type" size="sm" name="category" value={newData.category} onChange={handleNewDataChange}/>
          </Form.Group>
          
          <Form.Group className="mb-3" controlId="lat">
            <Form.Label>Latitude</Form.Label>
            <Form.Control type="text" placeholder="Enter Latitude" size="sm" name="lat" value={newData.lat} onChange={handleNewDataChange}/>
          </Form.Group>

          <Form.Group className="mb-3" controlId="long">
            <Form.Label>Longitude</Form.Label>
            <Form.Control type="text" placeholder="Enter Longitude" size="sm" name="long" value={newData.long} onChange={handleNewDataChange}/>
          </Form.Group>

        </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={(e) => {addcollector(modalAction)}}>
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );

};
export default Collectors;